import React, {Component} from 'react';
import Msj from '../../msj/Msj';
import Calendar from '../../calendar/Calendar'

import vendedora from '../../../img/vendedoras.png'
import { myFirstContext } from '../../Layout';


 class ViewOne extends Component{

    render(){
        return(
            <React.Fragment>
                <myFirstContext.Consumer>
            {value => {
                return(
                    <React.Fragment>
                        <Msj 
                            name={value.name}
                            msj={value.op}
                        />
                        <div className="asesoraMsj">
                            <img src={vendedora} alt="vendedora"/> 
                        </div>
                        <div className="ordenPedido">
                            <h3>Orden de pedido:</h3>
                            <h2>{value.op}</h2>
                        </div>
                            <Calendar op={value.op}/>
                         
                        <div className="compra">
                            <h3>TU COMPRA</h3>
                            {
                             value.compra.map((item, index) => {
                                return(
                                    <ul key={index} className="listCompra">
                                        <li className="productName">{item.producto}</li>
                                        <li><span>Cod:</span> {item.cod}</li>
                                        <li><span>Cantidad:</span> {item.cantidad}</li>
                                    </ul>
                                    )
                                })
                            } 
                        </div>
                    </React.Fragment> 
                    )
                }
            }
        </myFirstContext.Consumer> 
            </React.Fragment>
            
        )
    }
}
export default ViewOne ;