import React from 'react';
import './App.scss';
import Layout from './Layout';
import ViewOne from './views/view1/ViewOne'
import ViewTwo from './views/view2/ViewTwo'
import { BrowserRouter, Route, Switch } from 'react-router-dom'

function App() {
  return (
    <BrowserRouter>
      <Layout>
      <Switch>
        <Route exact path="/" component={ ViewOne }/>
        <Route exact path="/envios/:opId" component={ ViewTwo }/>
      </Switch>
      </Layout>
    </BrowserRouter>
      
  );
}

export default App;
