
import React, { Component } from "react";
import Header from './header/Header';
import logoBlanco from '../img/logoBlanco2.png';
import Api from '../utils/api';
// import moment from 'moment';

export const myFirstContext = React.createContext({
    name: "",
    op: "",
    compra: [],
    dia: "123",
})

class Layout extends Component {
    constructor(props){
        super()
        this.state = {
            name: "",
            op: "",
            compras: [],    
                 
        }
    }
    async componentDidMount(){
        const pedidos = await Api.getpedidos();

       this.setState({
            name: pedidos.OP[0].cliente,
            op: pedidos.OP[0].OP,
            compras: pedidos.OP[0].compra
        }) 
    }
render(){
    return (
        <div className="layout">
            <Header />
            <myFirstContext.Provider value={{
                name: this.state.name,
                op: this.state.op,
                compra: this.state.compras,
                dia: (date)=>{ return date },
            }}
            >
            {this.props.children}
            </myFirstContext.Provider>
          
            <div className="footer">
                <img src={logoBlanco} alt="jamar"/>
                <a href="https://www.jamar.com/">www.jamar.com</a> 
            </div>
        </div>
    );
    }   
}
export default Layout;