import React , { Component } from 'react'
import './Calendar.scss';

import 'react-infinite-calendar/styles.css';
import DatePicker from '@trendmicro/react-datepicker';
import '@trendmicro/react-datepicker/dist/react-datepicker.css';
import { Link } from 'react-router-dom';
import moment from 'moment';

import { myFirstContext } from '../Layout';
 moment.locale();
export default class Calendar extends Component {
    constructor(props){
        super(props);
        this.state = {
            date:  moment().format('LL')
        }
       
    }
    handleDate = (date) =>{
        const fecha = moment(date).format('LL')
        this.setState(state => ({ date: fecha }));   
        console.log( moment.locale('es'))
    }
    render(){
        
        return(
           <React.Fragment>
                
                <div className="calendar">
                    <div className="content-calendario">
                        <div className="header-date">
                        <p>Fecha sugerida</p> 
                        <h2>
                        <myFirstContext.Consumer>
                            {value =>( value.dia = (this.state.date) )
                                
                            }
                            </myFirstContext.Consumer> 
                        </h2>
                    </div>
                   
                    <DatePicker
                            date={this.state.date}
                            onSelect={ this.handleDate }
                    />

                        <button className="btncalendar">
                            <Link to={`/envios/${this.props.op}`}>Enviar fecha</Link>
                        </button>
                    </div>
                   
               </div>
               
                
           </React.Fragment>  
        )
    }
}