const api= 'https://s3.amazonaws.com/cloud.jamar.app/json_Vendedores/pedido.json';

class Api {
    async getPin(op){
        const query = await fetch(`https://s3.amazonaws.com/cloud.jamar.app/oplink/${op}.json`);
        const data = await query.json();
        return data
    }
    async getpedidos(){
        const query = await fetch(api);
        const data = await query.json();
        return data
    }
}
export default new Api();