import React, {Component} from 'react';
import Api from '../../../utils/api';
import Caja from '../../../img/cajavoladora.png'
import { myFirstContext } from '../../Layout';


 class ViewTwo extends Component{
    constructor(props){
        super();
        this.state = {
            pin: "",
                        
        }
    }
    async componentDidMount(){
        const id = this.props.match.params.opId;
        const pins = await Api.getPin(id);

        this.setState({
           pin: pins.pin
        })
    }
    render(){
        return(
            <div className="entrega">
                
                <div className="cajaCntent">
                    <img src={Caja} alt="enviamos"/>
                </div>            
                <myFirstContext.Consumer>
                       {value =>{
                           return(
                            <h2>¡Listo!, la fecha que seleccionaste fue:<br /> {value.dia} </h2>
                            
                           )
                       }}
                   </myFirstContext.Consumer>

                <hr />
                <h2 className="liteCode">Tu código pin es: <br /><span className="pinclass">{this.state.pin}</span></h2>
                <div className="recuerda">
                    <p>Recuerda estar en casa
                        o dejarle tu  <span>pin: {this.state.pin}</span> a alguien
                        para recibir tu pedido.
                    </p>
                   
                </div>
            </div>
        )
    }
}

  export default ViewTwo;